import {
  CategoryCardsInterface,
  CategoryDataInterface,
  ServerCardInterface,
} from '../api/api'; // eslint-disable-line import/extensions

const BASE_URL = 'https://lit-atoll-83744.herokuapp.com';

const PATH = {
  cards: 'categoriesCards',
  words: 'words',
};

export const getCategoriesCards = async (): Promise<CategoryCardsInterface[]> => (
  (await fetch(`${BASE_URL}/${PATH.cards}`)).json()
);

export const getCategoriesCardsItem = async (name: string): Promise<CategoryCardsInterface> => (
  (await fetch(`${BASE_URL}/${PATH.cards}/${name}`)).json()
);

export const deleteCategoriesCardsItem = async (name: string): Promise<void> => {
  await fetch(`${BASE_URL}/${PATH.cards}/${name}`, { method: 'DELETE' });
};

export const createCategoriesCardsItem = async (body = {}): Promise<CategoryCardsInterface> => (
  await fetch(`${BASE_URL}/${PATH.cards}`, {
    method: 'POST',
    mode: 'cors',
    body: JSON.stringify(body),
    headers: { 'Content-Type': 'application/json' },
  })
).json();

export const updateCategoriesCardsItem = async (
  name: string, newCategoryData: CategoryDataInterface,
): Promise<CategoryCardsInterface> => (
  await fetch(`${BASE_URL}/${PATH.cards}/${name}`, {
    method: 'PATCH',
    mode: 'cors',
    body: JSON.stringify(newCategoryData),
    headers: { 'Content-Type': 'application/json' },
  })
).json();

export const createWordItem = async (category: string, body = {}): Promise<ServerCardInterface> => (
  await fetch(`${BASE_URL}/${PATH.cards}/${PATH.words}/${category}`, {
    method: 'POST',
    mode: 'cors',
    body: JSON.stringify(body),
    headers: { 'Content-Type': 'application/json' },
  })
).json();

// TODO: add queries to endpoint
export const deleteWordItem = async (category: string, word: string): Promise<void> => {
  await fetch(`${BASE_URL}/${PATH.cards}/${PATH.words}/${word}`, {
    method: 'DELETE',
    body: category,
    headers: { 'Content-Type': 'text/html' },
  });
};
